<template>
    <div class="app flex-column">
        <div class="prize-list-tabs flex-column-shrink">
            <span :class="{'active': tabs==0}" @click="tabs=0">最新</span>
            <span :class="{'active': tabs==1}" @click="tabs=1">上周</span>
            <span :class="{'active': tabs==2}" @click="tabs=2">本月</span>
        </div>
        <div class="prize-list flex-column-grow" v-if="sscHistoryList">
            <div class="mui-scroll">
                <ul>
                    <template v-for="item in sscHistoryList">
                        <li :key="item.number">
                            <div class="a">
                                <div class="pict">
                                    <img :src="require(`@/static/img/play/${item.playGroupId}.png`)" alt="">
                                </div>
                                <div class="fl-layout two">
                                    <h2>{{params.name}}<p>{{item.number}}期 　{{item.openTime | timer}}</p></h2>
                                    <div class="number" v-if="item.openCode">
                                        <div class="number-component" v-if="playHandle(item.playGroupId)">
                                            <component :image="true" v-bind:is="'OpenCode-' + params.id" :number="item.openCode.split(',')"></component>
                                        </div>
                                        <div v-else>
                                            <span v-for="(item1, index) in item.openCode.split(',')" :key="index">{{item1}}</span>
                                        </div>
                                    </div>
                                    <div class="number" v-else>
                                        <p>正在开奖...</p>
                                    </div>
                                </div>
                            </div>
                        </li>
                    </template>
                </ul>
            </div>
        </div>
        <loadding v-else></loadding>
        <div class="fix-box" @click="$router.back()">
            <img src="@/static/img/6daf52_137x135.png" alt="">
        </div>
    </div>
</template>

<script>
import { newTime, lastWeek, thisMonth } from "@/tool/common/time.js";

console.log(thisMonth());

export default {
    data(){
        return {
            sscHistoryList: false,
            params: {},
            tabs: 0,
        }
    },
    created(){
        console.log(this.$route.query);
        this.params = this.$route.query
        this.getNetData()
    },
    watch:{
        tabs(){
            this.sscHistoryList = false
            setTimeout(() => {
                this.getNetData();
            }, 400);
        }
    },
    methods: {
        playHandle(id){
            let arr = [14, 15, 23, 27, 29, 20, 9, 22, 30, 31, 32, 6, 18, 19]
            return arr.contains(id);
        },
        getNetData(){

            let data = {
                startTime: '',
                endTime: '',
                pageSize: 50,
                pageIndex: 1,
                openDate:'',
                number:'',
                playGroupId: this.params.id,
            }

            switch (this.tabs) {
                case 0:
                    var t = newTime();
                    data.startTime = t.prev
                    data.endTime = t.next
                    break;
                case 1:
                    var t = lastWeek();
                    data.startTime = t.lastWday
                    data.endTime = t.fristWday
                    break;
                case 2:
                    var t = thisMonth();
                    data.startTime = t.thisMonthStart
                    data.endTime = t.thisMonthEnd
                    break;
                default:
                    break;
            }

            this.$api.game.getDataHistory(data)
            .then(res=>{
                console.log(res);
                this.sscHistoryList = res.sscHistoryList;
                this.mui_scroll('.prize-list');
            })
        },
    }
}
</script>

